import React from 'react'
import {ServicesContainer, ServicesCard, ServicesH1, ServicesIcon, ServicesP, ServicesWrapper, ServicesH2} from './ServicesElements'
import Icon1 from '../../images/svg-3.svg'
import Icon2 from '../../images/svg-4.svg'
import Icon3 from '../../images/svg-5.svg'

const Services = () => {
    return (
        <>
            <ServicesContainer id="services">
                <ServicesH1>
                    Our services
                </ServicesH1>
                <ServicesWrapper>
                    <ServicesCard>
                         <ServicesIcon src={Icon1}/>
                         <ServicesH2>Reduce expenses</ServicesH2>
                         <ServicesP>We help reduce your fees and increse your overall revenue.</ServicesP>
                    </ServicesCard>
                    <ServicesCard>
                         <ServicesIcon src={Icon2}/>
                         <ServicesH2>Virtual Offices</ServicesH2>
                         <ServicesP>You can access our platform online anywhere in the world.</ServicesP>
                    </ServicesCard>
                    <ServicesCard>
                         <ServicesIcon src={Icon3}/>
                         <ServicesH2>Premium benefits</ServicesH2>
                         <ServicesP>Unlock our special membership card that returns 5% cashb back.</ServicesP>
                    </ServicesCard>
                </ServicesWrapper>
            </ServicesContainer>   
        </>
    )
}

export default Services
